import { history } from "../../../history";
import * as Types from "../types";
import { Axios } from "../root"
import { displayErrorMessage, displayServerErrorMessage, displaySuccessMessage } from '../../../service/Alert';
import config from "../../../configs/config";

export const getData = (params) => {
  return async (dispatch, getState) => {
    await Axios.get(`/lessons/management?page=${params.page}&perPage=${params.perPage}`).then(response => {
      dispatch({
        type: Types.FETCH_LESSONS,
        data: response.data.lessons,
        totalPages: response.data.totalPages,
        params
      })
    })
  }
}

export const getDatabyId = (id) => {
  return async (dispatch, getState) => {
    await Axios.get(`/lesson/${id}`).then(response => {
      dispatch({
        type: Types.FETCH_LESSON_BY_ID,
        data: response.data.lesson,
      })
    })
  }
}

export const getInitialData = () => {
  return async (dispatch, getState) => {
    await Axios.get(`/lessons/management`).then(response => {
      dispatch({ type: Types.FETCH_ALL_LESSONS, data: response.data.lessons })
    })
  }
}

export const addData = (obj) => {
  return (dispatch, getState) => {
    let params = getState().lesson.params;
    let videos = obj.videos;

    let formData = new FormData();
    formData.append('name', obj.name);
    formData.append('level', obj.levelId);
    formData.append('youtubeUrl', obj.youtubeUrl);
    formData.append('videos', videos[0]);
    formData.append('learnkeys', JSON.stringify(obj.learnkeys));
    formData.append('description', obj.description);
    formData.append('image', obj.image[0]);
    formData.append('topic', obj.topic);

    Axios
      .post("/lesson/create", formData, {
        headers: { 'content-type': 'application/x-www-form-urlencoded' },
      })
      .then(response => {
        dispatch({ type: Types.ADD_LESSON, obj: response.data.doc })
        dispatch(getData(params));
        displaySuccessMessage('Successfully uploaded new lesson!')
      })
      .catch(displayServerErrorMessage)
  }
}

export const filterData = value => {
  return dispatch => dispatch({ type: Types.FILTER_LESSON , value })
}

export const updateData = obj => {
  return (dispatch, getState) => {
    let params = getState().lesson.params;

    let formData = new FormData();
    formData.append('name', obj.name);
    formData.append('youtubeUrl', obj.youtubeUrl);
    formData.append('videos', obj.videos[0]);
    formData.append('learnkeys', JSON.stringify(obj.learnkeys));
    formData.append('description', obj.description);
    formData.append('level', obj.levelId);
    formData.append('image', obj.image[0]);
    formData.append('topic', obj.topic);

    Axios
      .put("/lesson/" + obj._id, formData)
      .then(response => {
        dispatch({ type: Types.UPDATE_LESSON, obj })
        dispatch(getData(params))
        displaySuccessMessage('Successfully updated!')
      })
      .catch(displayServerErrorMessage)
  }
}
export const updateStatus = (obj, status) => {
  return (dispatch, getState) => {
    // let params = getState().lesson.params;
    Axios
      .put("/lesson/management/status/" + obj._id, {
        status: status
      })
      .then(response => {
        dispatch({ type: Types.UPDATE_LESSON_STATUS, obj, status: response.data.status })
        displaySuccessMessage('Successfully updated!')
      })
      .catch(displayServerErrorMessage)
  }
}

export const deleteData = obj => {
  return dispatch => {
    
    Axios
      .delete("/lesson/" + obj._id)
      .then(response => {
        dispatch({ type: Types.DELETE_LESSON, obj })
      })
      .catch(displayServerErrorMessage)
  }
}
export const deleteDataByIds = ids => {
  return dispatch => {
    
    Axios
      .put("/lesson-delete", {
        ids
      })
      .then(response => {
        dispatch({ type: Types.DELETE_LESSONS, ids })
        displaySuccessMessage('Successfully deleted!')
      })
      .catch(displayServerErrorMessage)
  }
}