import React, { Component } from 'react'
import { Row, Col} from "reactstrap"
import queryString from "query-string"
import ThumbViewConfig from "./DataListConfig"
import Breadcrumbs from "../../../components/@vuexy/breadCrumbs/BreadCrumb";
// import SelectTopic from "../../ui-elements/SelectTopic";

export class Index extends Component {
	render() {
		return (
			<React.Fragment>
				<Breadcrumbs
					breadCrumbTitle="Lesson management"
					breadCrumbParent="Area"
					breadCrumbActive="Data List"
				/>
        {/* <SelectTopic
          onChange={(value) => this.handleTopicChange(value)}
        /> */}
				<Row>
          <Col sm="12">
            <ThumbViewConfig thumbView={true} parsedFilter={queryString.parse(this.props.location.search)}/>
          </Col>
        </Row>

			</React.Fragment>
		)
	}
}

export default Index