import React, { Component } from "react"
import StatisticsCard from "../../../components/@vuexy/statisticsCard/StatisticsCard"
import {
  Button,
  UncontrolledDropdown,
  UncontrolledButtonDropdown,
  DropdownMenu,
  DropdownToggle,
  DropdownItem,
  Input
} from "reactstrap"
import DataTable from "react-data-table-component"
import ReactPaginate from "react-paginate"
import { Row, Col} from "reactstrap"
import * as Feather from "react-feather"
import { history } from "../../../history"
import {
  Edit,
  Trash,
  ChevronDown,
  Plus,
  Check,
  ChevronLeft,
  ChevronRight
} from "react-feather"
import { connect } from "react-redux"
import { Link } from "react-router-dom";
import StarRatings from 'react-star-ratings';
import {
  getData,
  getInitialData,
  deleteData,
  updateData,
  addData,
  filterData,
  deleteDataByIds,
  updateStatus
} from "../../../redux/actions/lesson"
import Chip from "../../../components/@vuexy/chips/ChipComponent"
import Checkbox from "../../../components/@vuexy/checkbox/CheckboxesVuexy"
import {subRooter} from "../../../configs/vars";
import { matchYMD } from "../../../service/helper";

import "../../../assets/scss/plugins/extensions/react-paginate.scss"
import "../../../assets/scss/pages/data-list.scss"
import "../../../assets/scss/custompages/lesson.scss";

const Status = {
  pending: "PENDING",
  accepted: "ACCEPTED",
  denied: 'DENIED',
}


const selectedStyle = {
  rows: {
    selectedHighlighStyle: {
      backgroundColor: "rgba(115,103,240,.05)",
      color: "#7367F0 !important",
      boxShadow: "0 0 1px 0 #7367F0 !important",
      "&:hover": {
        transform: "translateY(0px) !important"
      }
    }
  }
}

const ActionsComponent = props => {
  return (
    <div className="data-list-action">
      <Edit
        className="cursor-pointer mr-1"
        size={20}
        onClick={() => {
          return props.currentData(props.row)
        }}
      />
      <Trash
        className="cursor-pointer"
        size={20}
        onClick={() => {
          props.deleteRow(props.row)
        }}
      />
    </div>
  )
}

const CustomHeader = props => {
  return (
    <div className="data-list-header d-flex justify-content-between flex-wrap" style={{paddingTop: 5}}>
      <div className="actions-left d-flex flex-wrap">
        {/* <UncontrolledDropdown className="data-list-dropdown mr-1">
          <DropdownToggle className="p-1" color="primary">
            <span className="align-middle mr-1">Actions</span>
            <ChevronDown size={15} />
          </DropdownToggle>
          <DropdownMenu tag="div" right>
            <DropdownItem tag="button"
              onClick={() => props.handleActions('delete')}
            >Delete</DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown> */}
        <Link to={`${subRooter}/lesson/lesson-form`}>
          <Button
            className="add-new-btn mr-1"
            color="primary"
            outline
          >
            <Plus size={15} />
            <span className="align-middle">Add New</span>
          </Button>
        </Link>
        <Button
          className=""
          color="danger"
          onClick={() => props.handleActions('delete')}
          style={{marginBottom: 3}}
        >
          <Trash size={15} />
          <span className="align-middle">Delete</span>
        </Button>
      </div>
      <div className="actions-right d-flex flex-wrap mt-sm-0 mt-2">
        <UncontrolledDropdown className="data-list-rows-dropdown mr-1 d-md-block d-none">
          <DropdownToggle color="" className="sort-dropdown">
            <span className="align-middle mx-50">
              {`${props.index[0]} - ${props.index[1]} of ${props.total}`}
            </span>
            <ChevronDown size={15} />
          </DropdownToggle>
          <DropdownMenu tag="div" right>
            <DropdownItem tag="a" onClick={() => props.handleRowsPerPage(4)}>
              4
            </DropdownItem>
            <DropdownItem tag="a" onClick={() => props.handleRowsPerPage(10)}>
              10
            </DropdownItem>
            <DropdownItem tag="a" onClick={() => props.handleRowsPerPage(15)}>
              15
            </DropdownItem>
            <DropdownItem tag="a" onClick={() => props.handleRowsPerPage(20)}>
              20
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
        <div className="filter-section">
          <Input type="text" onChange={e => props.handleFilter(e)} />
        </div>
      </div>
    </div>
  )
}

class DataListConfig extends Component {
  static getDerivedStateFromProps(props, state) {
    if (
      props.dataList.data.length !== state.data.length ||
      state.currentPage !== props.parsedFilter.page
    ) {
      return {
        data: props.dataList.data,
        allData: props.dataList.filteredData,
        totalPages: props.dataList.totalPages,
        currentPage: parseInt(props.parsedFilter.page) - 1,
        rowsPerPage: parseInt(props.parsedFilter.perPage),
        totalRecords: props.dataList.totalRecords,
        sortIndex: props.dataList.sortIndex
      }
    }
    // Return null if the state hasn't changed
    return null
  }

  state = {
    data: [],
    totalPages: 0,
    currentPage: 0,
    columns: [],
    allData: [],
    value: "",
    rowsPerPage: 4,
    sidebar: false,
    currentData: null,
    selected: [],
    totalRecords: 0,
    sortIndex: [],
    addNew: ""
  }

  thumbView = this.props.thumbView

  componentDidMount() {
    let params = Object.keys(this.props.parsedFilter).length
      ? this.props.parsedFilter
      : { page: 1, perPage: 4 }
    this.props.getData(params)
    this.props.getInitialData();
    this.changeColumn();

  }

  changeColumn = () => {
    this.setState({
      columns: [
        {
          name: "Image",
          selector: "img",
          minWidth: "220px",
          cell: row => <img src={row.imgUrl} height="100" alt={row.name} />
        },
        {
          name: "Lesson",
          selector: "name",
          sortable: true,
          minWidth: "250px",
          cell: row => (
            <p title={row.name} className="text-truncate text-bold-500 mb-0">
              {row.name}
            </p>
          )
        },
        {
          name: "Topic",
          selector: "topic.name",
          sortable: true,
          cell: row => (`${row.topic && row.topic.name}`)
        },
        {
          name: "Level",
          selector: "level.name",
          sortable: true,
          cell: row => (`${row.level && row.level.name}`)
        },
        {
          name: "Category",
          selector: "topic.categoryId.name",
          sortable: true,
          cell: row => (
            `${row.topic && row.topic.categoryId && row.topic.categoryId.name}`
          )
        },
        {
          name: "Stars",
          // selector: "popularity",
          center: true,
          minWidth: "200px",
          sortable: true,
          cell: row => (
            <div className='d-flex p-relative'>
              <StarRatings
                rating={3}
                starRatedColor="yellow"
                starHoverColor='yellow'
                starDimension='20px'
                name='rating'
                isSelectable={false}
                starSpacing='1px'
              />
              <span className='d-block' style={{paddingTop: 2}}>2.4/255</span>
            </div>
          )
        },
        {
          name: "Lesson Status",
          selector: "status",
          center: true,
          sortable: true,
          cell: row => (
          <div className="dropdown d-inline-block">
            <UncontrolledButtonDropdown>
              <DropdownToggle color={`${row.status === 1 ? "success" : (row.status === 0 ? "warning" : "danger")}`} style={{zIndex: 10}} caret
                disabled={this.props.userRole !== 'admin'}
              >
                {
                  row.status === 1 ? "accepted" : (row.status === 0 ? "Pending" : "Deny") 
                }
                <ChevronDown size={15} />
              </DropdownToggle>
              <DropdownMenu>
                <DropdownItem tag="a" 
                  onClick={() => this.handleChangeLessonStatus(row, Status.pending)}
                >Pending</DropdownItem>
                <DropdownItem tag="a" 
                  onClick={() => this.handleChangeLessonStatus(row, Status.accepted)}
                >Accept</DropdownItem>
                <DropdownItem tag="a" 
                  onClick={() => this.handleChangeLessonStatus(row, Status.denied)}
                >Deny</DropdownItem>
              </DropdownMenu>
            </UncontrolledButtonDropdown>
          </div>
          )
        },
        {
          name: "Created By",
          selector: "createdBy.name",
          sortable: true,
          cell: row => (
            row.createdBy ? row.createdBy.firstname + " " + row.createdBy.lastname : ""
          )
        },
        {
          name: "Created At",
          sortable: true,
          cell: row => (matchYMD(row.createdAt))
        },
        {
          name: "Updated At",
          sortable: true,
          cell: row => (matchYMD(row.updatedAt))
        },
        {
          name: "Actions",
          sortable: true,
          cell: row => (
            <ActionsComponent
              row={row}
              getData={this.props.getData}
              parsedFilter={this.props.parsedFilter}
              currentData={this.handleCurrentData}
              deleteRow={this.handleDelete}
            />
          )
        }
      ],
    })
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.thumbView) {
      this.thumbView = false
    }
    if(!Object.is(this.state.data, prevState.data)) {
      this.changeColumn();
    }
  }

  handleFilter = e => {
    this.setState({ value: e.target.value })
    this.props.filterData(e.target.value)
  }

  handleRowsPerPage = value => {
    let { parsedFilter, getData } = this.props
    let page = parsedFilter.page !== undefined ? parsedFilter.page : 1
    history.push(`/myarea/lesson?page=${page}&perPage=${value}`)
    this.setState({ rowsPerPage: value })
    getData({ page: parsedFilter.page, perPage: value })
  }

  handleSidebar = (boolean, addNew = false) => {
    this.setState({ sidebar: boolean })
    if (addNew === true) this.setState({ currentData: null, addNew: true })
  }

  handleDelete = row => {
    this.props.deleteData(row);
    let params = Object.keys(this.props.parsedFilter).length
    ? this.props.parsedFilter
    : { page: 1, perPage: 4 }
    this.props.getData(params)
    if (this.state.data.length - 1 === 0) {
      let urlPrefix = "/myarea/lesson"
      history.push(
        `${urlPrefix}?page=${parseInt(
          this.props.parsedFilter.page - 1
        )}&perPage=${this.props.parsedFilter.perPage}`
      )
      this.props.getData({
        page: this.props.parsedFilter.page - 1,
        perPage: this.props.parsedFilter.perPage
      })
    }
  }

  handleCurrentData = obj => {
    this.setState({ currentData: obj })
    // this.handleSidebar(true)
    history.push(`${subRooter}/lesson/lesson-form?type=edit&id=${obj._id}`);
  }

  handlePagination = page => {
    let { parsedFilter, getData } = this.props
    let perPage = parsedFilter.perPage !== undefined ? parsedFilter.perPage : 4
    let urlPrefix = "/myarea/lesson"
    history.push(
      `${urlPrefix}?page=${page.selected + 1}&perPage=${perPage}`
    )
    getData({ page: page.selected + 1, perPage: perPage })
    this.setState({ currentPage: page.selected })
  }

  handleActions = (type) => {
    if(type === 'delete') {
      let ids = this.state.selected.map((elt) => elt._id);
      if(ids.length > 0) {
        this.props.deleteDataByIds(ids);
      }
    }
  }

  getActivedLessonCount = () => {
    return this.props.dataList.allData.filter((lesson) => lesson.status === 1).length
  }
  getPendingLessonCount = () => {
    return this.props.dataList.allData.filter((lesson) => lesson.status === 0).length
  }
  getDeniedLessonCount = () => {
    return this.props.dataList.allData.filter((lesson) => lesson.status === 2).length
  }
  getAllLessonCount = () => {
    return this.props.dataList.allData.length
  }

  handleChangeLessonStatus = (data, selectedStatus) => {
    if(data.status !== selectedStatus) {
      this.props.updateStatus(data, selectedStatus)
    }
  }

  render() {
    let {
      columns,
      data,
      allData,
      totalPages,
      value,
      rowsPerPage,
      totalRecords,
      sortIndex
    } = this.state;

    let activedCount = this.getActivedLessonCount();
    let pendingCount = this.getPendingLessonCount();
    let allCount = this.getAllLessonCount();
    let denyCount = this.getDeniedLessonCount();

    return (
      <React.Fragment>
         <Row>
          <Col xl="3" lg="3" sm="6">
            <StatisticsCard
              hideChart
              iconBg="success"
              icon={<Feather.Feather className="warning" size={22} />}
              stat={allCount}
              statTitle="All Lessons"
            />
          </Col>

					<Col xl="3" lg="3" sm="6">
            <StatisticsCard
              hideChart
              iconBg="primary"
              icon={<Feather.Link className="primary" size={22} />}
              stat={activedCount}
              statTitle="Active Lessons"
            />
          </Col>
          <Col xl="3" lg="3" sm="6">
            <StatisticsCard
              hideChart
              iconBg="success"
              icon={<Feather.Watch className="success" size={22} />}
              stat={pendingCount}
              statTitle="Pending Lessons"
            />
          </Col>
          <Col xl="3" lg="3" sm="6">
            <StatisticsCard
              hideChart
              iconBg="success"
              icon={<Feather.Link2 className="success" size={22} />}
              stat={denyCount}
              statTitle="Denied Lessons"
            />
          </Col>
        </Row>  
        <div
          className={`data-list thumb-view`}>
          <DataTable
            columns={columns}
            data={value.length ? allData : data }
            pagination
            paginationServer
            paginationComponent={() => (
              <ReactPaginate
                previousLabel={<ChevronLeft size={15} />}
                nextLabel={<ChevronRight size={15} />}
                breakLabel="..."
                breakClassName="break-me"
                pageCount={totalPages}
                containerClassName="vx-pagination separated-pagination pagination-end pagination-sm mb-0 mt-2"
                activeClassName="active"
                forcePage={
                  this.props.parsedFilter.page
                    ? parseInt(this.props.parsedFilter.page - 1)
                    : 0
                }
                onPageChange={page => this.handlePagination(page)}
              />
            )}
            noHeader
            subHeader
            selectableRows
            responsive
            pointerOnHover
            selectableRowsHighlight
            onSelectedRowsChange={data =>
              this.setState({ selected: data.selectedRows })
            }
            customStyles={selectedStyle}
            subHeaderComponent={
              <CustomHeader
                handleSidebar={this.handleSidebar}
                handleFilter={this.handleFilter}
                handleRowsPerPage={this.handleRowsPerPage}
                rowsPerPage={rowsPerPage}
                total={totalRecords}
                index={sortIndex}
                handleActions={this.handleActions}
              />
            }
            sortIcon={<ChevronDown />}
            selectableRowsComponent={Checkbox}
            selectableRowsComponentProps={{
              color: "primary",
              icon: <Check className="vx-icon" size={12} />,
              label: "",
              size: "sm"
            }}
          />
        </div>
      </React.Fragment>
    )
  }
}

const mapStateToProps = state => {
  return {
    userRole: state.auth.userRole,
    dataList: state.lesson
  }
}

export default connect(mapStateToProps, {
  getData,
  deleteData,
  updateData,
  addData,
  getInitialData,
  filterData,
  deleteDataByIds,
  updateStatus
})(DataListConfig)
